import { Injectable } from '@angular/core';
import { catchError, map } from 'rxjs/operators'
import { ServiceBag, bookingModel } from '@app/shared';
import { environment } from '@app/env';



@Injectable()
export class PastReservationsService {


  constructor(private http: ServiceBag) { }

  baseUrl = environment.backEndApi;
  serviceUrl = this.baseUrl + 'Doctor';

  loadPastReservations(params: any) {
    return this.http.get<bookingModel[]>(this.serviceUrl + '/GetPastBookings', params).pipe(map(res => {
      return res;
    }));
  }

}
