import { PageParams } from './../utils/models/app-model';
import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';
import { AuthService } from '@app/shared';
import { PastReservationsService } from '../utils';

@Component({
  selector: 'app-reservation-past',
  templateUrl: './reservation-past.component.html'
})
export class ReservationPastComponent implements OnInit {

  constructor(private authService: AuthService, private service: PastReservationsService) { }

  currentUser = this.authService.CurrentUser;
  listModel: any;
  params: PageParams = new PageParams();

  ngOnInit() {
    $(document).ready(function () {
      let slide = 'none';
      $('#down-past').click(function () {

        if (slide === 'none') {
          $('.slide-down').slideDown();
          slide = 'block';
        } else {
          $('.slide-down').slideUp();
          slide = 'none';
        }
      });
      $('.rate-box-item').click(function () {
        $('#back-rate').css('display', 'flex');
      });
      $('.submit , #submit-back-three ,#submit-back-one , #submit-back-two').click(function () {
        $('#back-rate').fadeOut();
      });
      $('.rate-chose').click(function () {
        $(this).addClass('rate-active');
        let idChose = $(this).attr('id');
        $('.rate-chose').not(this).removeClass('rate-active');
        $('.rate-chose').not(this).addClass('rateNotChose');
        $('.descriptionChose').fadeOut();
        $('#' + idChose + '-description').fadeIn();
      });
      $(document).mouseup(function (e) {
        let container = $('.box-rate');
        if (!container.is(e.target)
          && container.has(e.target).length === 0) {
          $('#back-rate').hide();
        }
      });
    });
    this.getPastReservations();
  }


  getPastReservations() {
    this.params.token = "3e40dbc2-1c77-4433-84cb-7947d2320f10";
    this.service.loadPastReservations(this.params).subscribe(res => {
      this.listModel = res.result;
      console.log(res)
    });
  }


}
